
          @import "@/assets/style/default.scss";
        



































































.att-sidebar-wrapper {
  width: 270px !important;
  max-width: 270px !important;
  padding-right: 0 !important;
  @media (max-width: 960px) {
    width: 220px !important;
    max-width: 220px !important;
    padding-right: 15px !important;
  }
  @media (max-width: 700px) {
    width: 100% !important;
    max-width: 100% !important;
  }
  .att-filter-block {
    margin-bottom: 50px;
    @media (max-width: 700px) {
      margin-bottom: 25px;
    }
  }
}
.att-select {
  background: url("../assets/img/icn_arrow_down.png") no-repeat calc(100% - 10px) center !important;
  border-radius: 0 !important;
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
  }
}
.att-filter-title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  @media (max-width: 700px) {
    margin-bottom: 5px;
  }
}
.att-list-of-category {
  .att-category-item {
    margin-bottom: 7px;
    @media (max-width: 700px) {
      display: inline-block;
      width: 50%;
    }
  }
  .att-category-count {
    color: #a6a9af;
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    margin-left: 5px;
  }
  .att-category-title {
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    &:before,
    &:after {
      top: .16rem !important;
      border: none !important;
      width: 18px !important;
      height: 18px !important;
      border-radius: 0 !important;
    }
    &:before {
      background: url("../assets/img/checkbox_base.png") !important;
    }
  }
  .custom-checkbox .custom-control-input:checked~.att-category-title:after {
    background: url("../assets/img/checkbox_checked.png") !important;
  }
}
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (orientation: landscape) {
  .att-sidebar-wrapper {
    position: fixed !important;
    width: 300px !important;
  }
}
